<template>
  <div class="toast-box" v-show="message">
    {{ message }}
  </div>
</template>

<script>
export default {
  name: "ToastComponent",
  data() {
    return {
      message: ''
    }
  },
  methods: {
    toast(msg, delay) {
      this.message = msg
      let timer = setTimeout(() => {
        this.message = ''
        clearTimeout(timer)
      }, delay)
    }
  }
}
</script>

<style scoped>
.toast-box {
  position: fixed;
  z-index: 99;
  top: 50%;
  left: 50%;
  background-color: #eeeeee;
  padding: 5px;
  transform: translate(-50%, -50%);
  border-radius: 5px;
  width: 50vw;
  word-break: break-all;
  text-align: center;
}
</style>
