import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    cartList: [],
    cartListCount: 0
  },
  mutations: {
    doAddToCart(state, payload) {
      state.cartListCount++
      state.cartList.push(payload)
    },
    doAddCount(state, i) {
      state.cartList[i].count++
      state.cartListCount++
    },
    doChangeCheckBox(state, {id, checked}) {
      for (let i = 0; i < state.cartList.length; i++) {
        if (state.cartList[i].id === id) {
          state.cartList[i].checked = checked
          return
        }
        console.log(id)
      }
    },
    doCheckAll(state, payload) {
      for (let i = 0; i < state.cartList.length; i++) {
        state.cartList[i].checked = payload
      }
    }
  },
  actions: {
    doAddCart(context, payload) {
      return new Promise((resolve, reject) => {
        // 判断商品是否已经存在
        const len = context.state.cartList.length

        if (len > 0) {
          let i = 0
          for (i; i < len; i++) {
            if (payload.id === context.state.cartList[i].id) {
              context.commit('doAddCount', i)
              resolve('商品数量+1')
              return
            }
          }
          payload.count = 1
          context.commit('doAddToCart', payload)
          resolve('商品添加到购物车成功')
        } else {
          payload.count = 1
          context.commit('doAddToCart', payload)
          resolve('商品添加到购物车成功')
        }
      })
    }
  },
  modules: {}
})
